html, body {
  height: 100%;
  font-family: "Poppins", sans-serif;
}

body {
  margin: 0;
}

.app__snackbar {
  background-color: #009688;
}

.app__snackbar-error {
  background-color: #c62828 !important;
}

.mat-form-field-appearance-outline:not(.mat-form-field-disabled):not(.mat-form-field-invalid) .mat-form-field-flex:hover .mat-form-field-outline-thick {
  color: #009688;
}

.mat-simple-snackbar {
  color: white;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
}

.tooltip {
  font-size: 11px;
  font-family: "Poppins", sans-serif;
  white-space: pre-line;
  background-color: #009688;
}

.tooltip-error {
  background-color: #c62828;
  font-size: 11px;
  font-family: "Poppins", sans-serif;
  white-space: pre-line;
}

.dot {
  height: 7px;
  width: 7px;
  background-color: #009688;
  border-radius: 50%;
  display: inline-block;
  vertical-align: middle;
}

@keyframes button-spinner {
  to {
    transform: rotate(360deg);
  }
}

.button-spinner:before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  margin-top: -10px;
  margin-left: -10px;
  border-radius: 50%;
  border: 2px solid #ffffff;
  border-top-color: #000000;
  animation: button-spinner 0.8s linear infinite;
}

.header-title--key {
  color: #009688;
}

.header-title--value {
  white-space: nowrap;
}

.primary {
  color: #009688;
}
